<template>
    <v-container fluid class="qiong-txt-td qiong-pt-30">
        <qiong-header>購買記錄</qiong-header>

        <qiong-page-panel>
            <buy-records-table-header></buy-records-table-header>

            <buys-records-table :buy="buys" v-if="buys"></buys-records-table>

            <div class="qiong-bottom-spce"></div>
        </qiong-page-panel>
            
    </v-container>
</template>

<script>
import QiongHeader from '../../../components/Qiong/Pager/QiongHeader.vue'
import QiongPageHeader from '../../../components/Qiong/QiongPageHeader.vue'
import QiongPageLing from '../../../components/Qiong/QiongPageLing.vue'
import QiongPagePanel from '../../../components/Qiong/QiongPagePanel.vue'
import BuysRecordsTable from './Inner/BuysRecordsTable.vue'
import BuyRecordsTableHeader from './Inner/BuyRecordsTableHeader.vue'
import QiongLoading from '../../../components/Qiong/Ui/QiongLoading.vue'

    export default {
  components: { QiongPageHeader, QiongHeader, QiongPageLing, QiongPagePanel, BuyRecordsTableHeader, BuysRecordsTable, QiongLoading },
        name: '',
    
        data() {
            return {
                buys: null
            }
        },
        async mounted() {
            await this.buysLoading()
        },
        methods: {
            async buysLoading() {
                const res = await this.conn.get(this.api.buys, this.$store.state.token, {
                    'chronu': this.$store.state.chronus.id,
                    '_sort': 'published_at:DESC'
                })

                if (res) {
                    this.buys = [ ]
                    const transfer = this.$store.state.transfer

                    if (transfer) {

                        for (let i= 0; i< transfer.length; i++ ) {
                            const item = transfer[i]

                            for (let j= 0; j< res.length; j++ ) {

                                if (item.unid == res[j].users_permissions_user.unid) {
                                    this.buys.push(res[j])
                                }
                            } 
                        }
                    }

                }

            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


